<template>
<div class="container">
    <div class="row">
    <div class="col-12 text-center">
    <br>
     <h4> Welcome to {{this.$router.history.current.name}} !</h4>
    </div>
  </div>
    <div class="col-10 col-lg-6 user-form">
      <p> Proceed with any console command you need: </p>
      <p><b>Host</b>: {{this.$user.host}}</p>
      <p><b>Port</b>: {{this.$user.port}}</p>
      <p><b>What will happen</b>: cmd will executed your command</p>
      <vue-form-generator :schema="schema" :model="model"></vue-form-generator>
      <button class="user-button" v-on:click="InvokeCSharpWithFormValues(this, model)">Execute</button>
    </div>
</div>
</template>

<script>
import Vue from "vue";
import VueFormGenerator from "vue-form-generator";
import apiService from '../service/api.service.js';

Vue.use(VueFormGenerator);

let consoleCommands = {
  name: "consoleCommands",
  data() {
    return {
      model: {
        cmd: "",
      },
      schema: {
        fields: [
          {
            type: "input",
            // inputType: "password",
            label: "Command",
            model: "cmd",
            min: 1,
            required: true,
            hint: "Minimum 1 character",
            validator: VueFormGenerator.validators.string
         }
        ]
      },

      formOptions: {
        validateAfterLoad: true,
        validateAfterChanged: true
      }
    };
  },
  methods: {
    disabled: function (model) {
      for (const prop in model) {
          if (model[prop] == "") {
            return true;
          }
      }
      return false;
    },
    InvokeCSharpWithFormValues: function (elm, model) {
            if (this.disabled(model)) {
                toastr.warning("Please fill all form fields.");
                return false;
            }
            let data = {
              command: this.$router.history.current.name,
              params: {cmd: model.cmd}
            };

            let qs = "params=" + JSON.stringify(data);

            console.log(qs);

            location.href = "hybrid:" + "SendCommand" + "?" + qs;
    },
    handler: function(model) {
       if (disabled(model)) {
                toastr.warn("Please fill All fields.");
                return false;
        }
      let data = {
          user_id: this.$user.id,
          command_id: this.$router.history.current.params.commandId,
          params: {cmd: model.cmd}
        };


      apiService.sendCommand(data)
          .then((data) => {
              toastr.info("Console command was sent to execute");
          })
          .catch((err) => {
              toastr.error("Console command was not sent to execute");
          })
    }
  }
};

Vue.component("console-commands", consoleCommands);
export default consoleCommands;
</script>

style media="screen">
.user-form {
  border: none;
  border-radius: 40px;
  padding: 7px;
  text-align: center;
  margin: auto !important;
}
.hint {
  color: grey;
  font-size: 12px;
}
.errors,
.help-block {
  color: red;
  font-size: 12px;
}

.big-size {
  font-size: 25px !important;
}

.user-button {
  cursor: pointer;
  border-radius: 10px ;
  border: none;
  font-size: 25px;
  background-color: #159957;
  color: white;
  margin: auto;
  margin-top: 10px;
}

</style>


