<template>
<div class="container">
    <div class="row">
    <div class="col-12 text-center">
    <br>
     <h4> Welcome to {{this.$router.history.current.name}} !</h4>
    </div>
  </div>
    <div class="col-10 col-lg-6 user-form">
      <p> Proceed with any keyboard command you need: </p>
      <p><b>Host</b>: {{this.$user.host}}</p>
      <p><b>Port</b>: {{this.$user.port}}</p>
      <p><b>What will happen</b>: keyboard will executed your command</p>

      <input v-model="this.sendInput" placeholder="press some keys..." maxlength="16" type="text" readonly>
      <br>
      <keyboard
        v-model="input"
        @custom="custom"
        @input="changed"
        :layouts="[
          '1234567890{delete:backspace}|QWERTYUIOP|ASDFGHJKL|{shift:goto:0}ZXCVBNM|{space:space}{custom:custom}'
        ]"
        :maxlength="16"
      ></keyboard>
      <br>
      
      <button class="user-button" v-on:click="InvokeCSharpWithFormValues(this)">Execute</button>
    </div>
</div>
</template>

<script>
import Vue from "vue";
import VueFormGenerator from "vue-form-generator";
import apiService from '../service/api.service.js';
import keyboard from 'vue-keyboard';


Vue.use(keyboard);

let consoleCommands = {
  name: "consoleCommands",
   components: {
   keyboard
  },
  data() {
    return {
      input: '',
      sendInput: ''
    };
  },
  methods: {
    changed(value) {
      this.sendInput = value;
      console.log(this.sendInput);
    },
    custom(keyboard) {
      console.log(keyboard.value);
    },
    disabled: function (model) {
      for (const prop in model) {
          if (model[prop] == "") {
            return true;
          }
      }
      return false;
    },
    InvokeCSharpWithFormValues: function (elm, model) {
           
            let data = {
              command: this.$router.history.current.name,
              params: {key: this.sendInput.split('')}
            };

            let qs = "params=" + JSON.stringify(data);

            console.log(qs);

            location.href = "hybrid:" + "SendCommand" + "?" + qs;
    },
    handler: function(model) {
      let data = {
          user_id: this.$user.id,
          command_id: this.$router.history.current.params.commandId,
          params: {key: this.sendInput.split('')}
        };


      apiService.sendCommand(data)
          .then((data) => {
              toastr.info("Keyboard command was sent to execute");
          })
          .catch((err) => {
              toastr.error("Keyboard command was not sent to execute");
          })
    }
  }
};

Vue.component("keyboard-rr", consoleCommands);
export default consoleCommands;
</script>

style media="screen">
.user-form {
  border: none;
  border-radius: 40px;
  padding: 7px;
  text-align: center;
  margin: auto !important;
}
.hint {
  color: grey;
  font-size: 12px;
}
.errors,
.help-block {
  color: red;
  font-size: 12px;
}

.big-size {
  font-size: 25px !important;
}

.user-button {
  cursor: pointer;
  border-radius: 10px ;
  border: none;
  font-size: 25px;
  background-color: #159957;
  color: white;
  margin: auto;
  margin-top: 10px;
}

</style>


