<template>
<div class="container">
<h4> List of Processes</h4>
  <div class="row">
    <div class="col-12 holder-admin-journal">
     <div v-for="p in this.processes">
       <hr>
       <p><b>ID</b>: {{p.id}}</p>
       <p><b>Description</b>: {{p.description}}</p>
       <p><b>Memory</b>: {{p.memory}}</p>
     </div>
    </div>
  </div>
</div>
</template>

<script>
import Vue from "vue";
import accounting from "accounting";
import moment from "moment";
import Vuetable from "vuetable-2/src/components/Vuetable";
import VuetablePagination from "vuetable-2/src/components/VuetablePagination";
import VuetablePaginationInfo from "vuetable-2/src/components/VuetablePaginationInfo";
import apiService from '../service/api.service.js';

let processListCommand = {
  name: "processListCommand",
  data() {
    return {
     processes: [],
     link: "https://rr-test-vlada.herokuapp.com/api/processes-for-user?user_id=" + this.$user.id,
     fields: [
        {
          name: 'id',
          sortField: 'id',
        }, 
        {
          name: 'description',
          sortField: 'description'
        },
        {
          name: 'memory',
          sortField: 'memory'
        }
      ],
       sortOrder: [
        {
          field: "id",
          sortField: "id",
          direction: "asc"
        }
      ]
    }
  },
  methods: {
    clickHandler: function() {},
    onPaginationData(paginationData) {
      this.$refs.paginationTop.setPaginationData(paginationData);
      this.$refs.paginationInfoTop.setPaginationData(paginationData);
      this.$refs.pagination.setPaginationData(paginationData);
      this.$refs.paginationInfo.setPaginationData(paginationData);
    },
    onChangePage(page) {
      this.$refs.vuetable.changePage(page);
    }
  },
  created: function () {
    apiService.getProcesses(this.$user.id)
          .then((data) => {
              //this.processes = data.data;
          })
          .catch((err) => {
              this.processes = [];
          })
  }
};

Vue.component("process-list-command", processListCommand);
export default processListCommand;
</script>

<style media="screen">
.user-form {
  border: 2px solid green;
  border-radius: 40px;
  padding: 27px;
  text-align: center;
  margin: auto !important;
}
.hint {
  color: grey;
  font-size: 12px;
}
.errors,
.help-block {
  color: red;
  font-size: 12px;
}

.user-button {
  cursor: pointer;
  border-radius: 10px;
  font-size: 20px;
  background-color: #159957;
  color: white;
  margin: auto;
  margin-top: 10px;
}
#command {
    display: none;
}
</style>



